@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&family=Montserrat&display=swap");
.App {
	min-height: 100vh;
	height: auto;
	text-align: center;
	font-family: "Montserrat", sans-serif;
}

h1 {
	font-family: "Fredoka One", cursive;
	color: #ff7a8a;
	text-shadow: 2px 1px black;
}

pre {
	font-size: 24px;
}

hr {
	margin-top: 20px;
	width: 60%;
}

.inp {
	width: 300px;
	height: 50px;
	border: 2px solid #113a5d;
	margin: 0px 10px;
	padding-left: 6px;
	padding: 8px;
	font-size: 20px;
	border-radius: 5px;
}

.btn {
	width: 90px;
	height: 50px;
	font-size: 18px;
	background-color: orange;
	color: white;
	border: 1px solid #113a5d;
}

.mode{
	display: flex;
	flex-flow: row-reverse;
}
#a{
	margin-right: 5%;
	margin-top: 2%;
	background: transparent;
	border: none;
	position: fixed;
	border-radius: 50px;
}

@media only screen and (max-width: 1100px)
{
	#a
	{
		margin-right: 12%;
		margin-top: 3%;
	}
	h1{
		margin-top: 50px;
		margin-right: 15px;
	}
	.inp{
		margin-right: 10%;
		margin-top: 5%;
	}
	.btn{
		margin-top: 5%;
		margin-right: 10%;
	}
	hr{
		width: 100%;
	}
	pre{
		font-size: 10px;
		width: 90%;
	}
}